import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ConstantsService } from 'src/app/shared/services/constants.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as moment from 'moment';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';


@Component({
  selector: 'app-income-pay-invoice',
  templateUrl: './income-pay-invoice.component.html',
  styleUrls: ['./income-pay-invoice.component.scss']
})
export class IncomePayInvoiceComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  isInvoiceSelected:boolean = false;
  selectAllInvoices:boolean = false;

  totalAmountArray:any;
  selectedInvoiceIdArray:any;

  accountDataList:any;
  isAccountDataLoaded:boolean = false;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  columnData:any ;
  gridSourceData:any;

  constructor(
    private route: ActivatedRoute,
  	private accountsService:AccountsService,
    private sharedService:SharedService,
    private constantsService:ConstantsService,
  	private sessionService:SessionService
  ) { }

  isCollectionMade(event){
    if(event){

      this.totalAmountArray = [];
      this.selectedInvoiceIdArray = [];
      this.isInvoiceSelected = false;

      this.getCustInvoices();
    }
  }

  @HostListener('window:onCheckInvoiceHeader', ['$event.detail'])
  onCheckInvoiceHeader(detail) {

    let allDataRecords = this.datagrid.getrows()

    this.totalAmountArray = [];
    this.selectedInvoiceIdArray = [];

    allDataRecords.forEach(item => {
      item.checked = detail.checked
      this.totalAmountArray.push(item.remainingAmount)
      this.selectedInvoiceIdArray.push(item.custInvoiceID);
    });

    this.isInvoiceSelected = allDataRecords.some(item => {
      return item.checked
    })

    this.datagrid.refresh();

    setTimeout(() => {

      var elem = document.querySelector('.post-collection-comp');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  @HostListener('window:onCheckInvoiceRow', ['$event.detail'])
  onCheckInvoiceRow(detail) {
    
    let allDataRecords = this.datagrid.getrows()
    var dataRecord = this.datagrid.getrowdata(detail.rowId)

    dataRecord.checked = detail.checked;

    this.totalAmountArray = [];
    this.selectedInvoiceIdArray = [];

    allDataRecords.forEach(item => {
      if(item.checked){
        length++;
        this.totalAmountArray.push(item.remainingAmount);
        this.selectedInvoiceIdArray.push(item.custInvoiceID);
      }
    });

    this.isInvoiceSelected = allDataRecords.some(item => {
      return item.checked
    })

    setTimeout(() => {

      var elem = document.querySelector('.post-collection-comp');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'PayInvoiceData');
  }

  onGlSearchFilter() {

    if(this.invoiceData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.invoiceData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  renderColumns() {

    let cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    let columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: '',
      datafield: 'checked',
      width: 80,
      pinned: true,
      sortable: false,
      menu: false,
      cellsrenderer: (row, column, value) => {

         var chkedProperty;
         
         if(value){
           chkedProperty = 'checked="true"';
         }
         else {
           chkedProperty = '';
         }

         return '<div class="jqx-custom-inner-cell">'
                 + '<div class="form-group mb-0 w-100">'
                   +  '<div class="form-check text-center">'
                       + '<input type="checkbox" class="form-check-input" onClick="checkInvoiceRowEvent('+ row +', this.checked)" id="defaultChecker'+ row +'" name="defaultChecker'+ row +'" '+ chkedProperty +'>'
                       + '<label class="form-check-label" for="defaultChecker'+ row +'"></label>'
                   +  '</div>'
                 + '</div>'
              + '</div>'
      },
      renderer: (value) => {

       return '<div style="padding: 10px">'
       + '<div class="form-group mb-0 w-100">'
         +  '<div class="form-check check-header text-center">'
             + '<input type="checkbox" id="selectAllDefaulters" name="selectAllDefaulters" onClick="checkInvoiceHeaderEvent(event, this.checked)" >'
             + '<label class="form-check-label" for="selectAllDefaulters"></label>'
         +  '</div>'
       + '</div>'
      + '</div>'

      }
    },
    {
      text: 'Bill No',
      datafield: 'custInvoiceID',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Bill Date',
      datafield: 'custInvoiceDate',
      width: 120,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Due Date',
      datafield: 'dueDate',
      width: 120,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Comments',
      datafield: 'comments',
      minwidth: 180,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Bill Amount',
      datafield: 'billAmount',
      minwidth: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Remaining Amount',
      datafield: 'remainingAmount',
      minwidth: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }]

  }

  getCustInvoices() {

    let params = {
      ApartmentBlockUnitID: this.route.params['value'].id
    }

    this.accountsService.getArBalancewithDueByAptBlkUnitId(params).subscribe((res:any) => {

      var invoiceDataList = res;

      console.log(res)

      this.gridSourceData = {
        localdata: invoiceDataList,
        datatype: "array"
      }

      this.invoiceDataList = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = invoiceDataList.length;

      this.isInvoiceDataLoaded = true;
      
      this.renderColumns();

    })

  }

  ngOnInit() {

  this.getCustInvoices();

  let params = {
    apartmentId: this.sessionService.apartmentId
  }

  this.accountsService.getIncomeTrackerSubLedgersByApartmentId(params).subscribe((res:any) => {

      this.accountDataList = res.filter(item => {
        return item.apartmentBlockUnitId == this.route.params['value'].id;
      });

      this.isAccountDataLoaded = true;

  });

	var accountParams = {
      apartmentId: this.sessionService.apartmentId,
      groupId: 3
  }

  this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {
    this.glAccountListData = res;
  });

  this.sharedService.setActionIncomeTrackerIndex(this.route.params['value'].id)

  }

}


let checkInvoiceHeaderEvent = (event, isChecked) => {

  event.stopPropagation();
  var newEvent = new CustomEvent('onCheckInvoiceHeader', {
    detail : {
      checked: isChecked
    }
  })
  window.dispatchEvent(newEvent);

}
(window as any).checkInvoiceHeaderEvent = checkInvoiceHeaderEvent;

let checkInvoiceRowEvent = (row, isChecked) => {

  var event = new CustomEvent('onCheckInvoiceRow', {
    detail: {
        rowId: row,
        checked: isChecked
    }
  })
  window.dispatchEvent(event);

}
(window as any).checkInvoiceRowEvent = checkInvoiceRowEvent;
